;\n}\r\n\r\n.table-head .table-cell {\n\tfont-weight: 700;\n\tbackground-color: #f2f2f2;\n\tfont-size: 1.1em;\n\tpadding-top: 6px;\n\tpadding-bottom: 6px;\n}\r\n\r\n.table-padding .table-cell {\n    padding-left: 8px;\n    padding-right: 8px;\n}\r\n\r\n.table-padding .table-cell:first-child {\n    padding-left: 0px;\n}\r\n\r\n.table-padding .table-cell:last-child {\n    padding-right: 0px;\n}\r\n\r\n.table-border .table-cell {\n    border-bottom: 1px solid #f2f2f2;\n}\r\n\r\n.table-wide {\n    width: 100%;\n}\r\n\r\n.table-center .table-cell {\n\ttext-align: center;\n}\r\n\r\n.table-top .table-cell  {\n\tvertical-align: top;\n}\r\n\r\n/* *********** FORM ELEMENTS ********* */\r\n\r\ninput, select, textarea {\n  -webkit-box-sizing: border-box;\n          box-sizing: border-box;\n}\r\n\r\ninput, textarea, select {\n\tborder: 1px solid #eaeaea;\n    background-color: #fafafa;\n    color: #7b7b7b;\n    vertical-align: top;\n}\r\n\r\ninput {\n    height: 30px;\n    padding: 0px 6px;\n    font-size: 1em;\n    /*text-indent: 4px;*/\n\n    -webkit-box-shadow: none;\n    -o-box-shadow: none;\n    -ms-box-shadow: none;\n    box-shadow: none;\n}\r\n\r\ninput.auto {\n\twidth: auto;\n}\r\n\r\ninput.mid {\n    width: 50%;\n}\r\n\r\ninput.full {\n    width: 100%;\n}\r\n\r\ninput[type=\"text\"],\ninput[type=\"password\"],\ninput[type=\"email\"] {\n    min-width: 171px; /* defaut width of input */\n}\r\n\r\ninput[type=\"number\"] {\n    min-width: 84px;\n}\r\n\r\ninput[type=\"checkbox\"],\ninput[type=\"radio\"] {\n    cursor: pointer;\n    background-color: transparent;\n    margin-right: 3px;\n}\r\n\r\ninput[type=\"file\"] {\n    padding: 0;\n    border: 0;\n    cursor: pointer;\n}\r\n\r\ninput[type=\"submit\"] {\n    background-color: #eaeaea;\n}\r\n\r\nselect {\n    height: 30px;\n    padding: 0 2px;\n    min-width: 171px; /* defaut width of input */\n    cursor: pointer;\n}\r\n\r\nselect[multiple] {\n\theight: auto;\n\toverflow: none;\n}\r\n\r\nselect option {\n    padding: 0 4px;\n}\r\n\r\ntextarea {\n    padding: 8px 6px;\n    min-height: 92px;\n    font-size: 1em;\n\n    resize: none;\n}\r\n\r\ntextarea.wide {\n    width: 100%;\n    min-width: 100%;\n    max-width: 100%;\n}\r\n\r\ntextarea.resizable {\n\tresize: vertical;\n\t/*resize: none | both | horizontal | vertical | inherit;*/\n}\r\n\r\ninput:invalid:focus {\n    /*border: 1px solid #2972ad;*/\n    color: #ee586a;\n\n    /*\n    -webkit-box-shadow: inset 0 0 0 1px #ee586a;\n    -moz-box-shadow: inset 0 0 0 1px #ee586a;\n    -o-box-shadow: inset 0 0 0 1px #ee586a;\n    -ms-box-shadow: inset 0 0 0 1px #ee586a;\n    box-shadow: inset 0 0 0 1px #ee586a;\n    */\n}\r\n\r\ninput:required {\n    /* necessaire pour ecraser default */\n    -webkit-box-shadow: none;\n    -o-box-shadow: none;\n    -ms-box-shadow: none;\n    box-shadow: none;\n}\r\n\r\n::-webkit-input-placeholder {\n   color: #7b7b7b !important;\n   opacity: 0.4;\n}\r\n\r\n:-moz-placeholder { /* Firefox 18- */\n   color: #7b7b7b !important;\n   opacity: 0.4;\n}\r\n\r\n::-moz-placeholder{ /* Firefox 19+ */\n   color: #7b7b7b !important;\n   opacity: 0.4;\n}\r\n\r\n:-ms-input-placeholder {\n   color: #7b7b7b !important;\n   opacity: 0.4;\n}\r\n\r\nlabel {\n    /*height: 30px;*/\n    line-height: 30px;\n\n    display: inline-block;\n    *display: inline;\n    *zoom: 1;\n    /*vertical-align: middle;*/\n    vertical-align: baseline;\n}\r\n\r\nlabel[for] {\n\tcursor: pointer;\n\n    -webkit-touch-callout: none;\n    -webkit-user-select: none;\n    -khtml-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n    -user-select: none;\n}\r\n\r\nlabel:first-letter {\n    text-transform: capitalize;\n}\r\n\r\n/*\n.checkline {\n    line-height: 20px;\n}\n.checkline input[type=\"checkbox\"],\n.checkline input[type=\"radio\"] {\n    line-height: 20px;\n    height: 20px;\n    width: auto;\n    margin-right: 3px;\n}\n.checkline label,\n.checkline .label {\n    line-height: 20px;\n    height: 20px;\n    vertical-align: middle;\n}\n*/\r\n\r\n.span-label {\n    line-height: 30px;\n    display: inline-block;\n    *display: inline;\n    *zoom: 1;\n    vertical-align: top;\n}\r\n\r\n.label-wrap { /* prévu pour label dans avec input à coté */\n    margin-right: 1em;\n\n    display: inline-block;\n    *display: inline;\n    *zoom: 1;\n    /*vertical-align: middle;*/\n    vertical-align: top;\n}\r\n\r\n.label-up { /* prévu pour label au dessus de l'input ou autre */\n    margin-top: 1em;\n    margin-bottom: 0.3em;\n}\r\n\r\n.label-mbtm { /* prévu pour label au dessus de l'input ou autre DANS FORMULAIRE */\n    margin-bottom: 0.6em;\n}\r\n\r\n.label-up label,\n.label-mbtm label {\n    height: auto;\n    line-height: 1.2;\n}\r\n\r\n.label-pack { /* prévu pour encadrer label inline et texte avec peu de margin */\n    margin-bottom: 0.1em;\n}\r\n\r\n.label-pack label {\n    font-weight: 700;\n    display: inline;\n    line-height: 1.2;\n    height: auto;\n}\r\n\r\n/* ******************************** BUTTONS ******* */\r\n\r\n.btn {\n    background-color: #eaeaea;\n\tcolor:#000000;\n\n    text-align: center;\n    border: 0px none;\n    outline: 0;\n\n    /*\n    height: 26px;\n    line-height: 26px;\n    padding: 0px 16px;\n    vertical-align: bottom;\n    */\n\n    height: auto;\n    padding: 5px 16px;\n    line-height: 1.2;\n    /*vertical-align: bottom;*/\n    vertical-align: baseline;\n    min-height: 30px; /* hauteur des inputs */\n\n    text-decoration: none;\n    font-weight: normal;\n    font-size: 1em;\n    text-indent: 0px;\n    cursor: pointer;\n\n    -webkit-transition: all 0.1s ease-out;\n    transition: all 0.1s ease-out;\n    border-radius: 2px;\n}\r\n\r\n.btn:first-letter {\n    text-transform: capitalize;\n}\r\n\r\n.btn:hover {\n    background-color: #f2f2f2;\n}\r\n\r\n.btn:active {\n    -webkit-transform: translateY(1px);\n    transform: translateY(1px);\n}\r\n\r\na.btn {\n    *display: inline;\n    *zoom: 1;\n    display: inline-block;\n    /*vertical-align: text-bottom;*/ /* pas cool sous webkit mac */\n    vertical-align: top;\n    line-height: 20px;\n}\r\n\r\n.btn_black {\n    background-color: #000000 !important;\n    color: #ffffff;\n}\r\n\r\n.btn_white {\n    background-color: #ffffff !important;\n}\r\n\r\n.btn_grey1 {\n    background-color: #999999 !important;\n    color: #ffffff;\n}\r\n\r\n.btn_grey2 {\n    background-color: #eaeaea !important;\n}\r\n\r\n.btn_color1 {\n    background-color: #ed5137 !important;\n    color: #ffffff;\n}\r\n\r\n.btn_color2 {\n    background-color: #f72e35 !important;\n    color: #ffffff;\n}\r\n\r\n.btn_color3 {\n    background-color: #24c96e !important;\n    color: #ffffff;\n}\r\n\r\n.btn_black:hover {\n    background-color: #111111 !important;\n}\r\n\r\n.btn_white:hover {\n    background-color: #f2f2f2 !important;\n}\r\n\r\n.btn_grey1:hover {\n    background-color: #aaaaaa !important;\n}\r\n\r\n.btn_grey2:hover {\n    background-color: #f2f2f2 !important;\n}\r\n\r\n.btn_color1:hover {\n    background-color: #e0442a !important;\n}\r\n\r\n.btn_color2:hover {\n    background-color: #FF494F !important;\n}\r\n\r\n.btn_color3:hover {\n    background-color: #37d67c !important;\n}\r\n\r\n.btn.btn_big {\n    line-height: 30px;\n    padding-left: 26px;\n    padding-right: 26px;\n}\r\n\r\n.btn > *:last-child { /* to remove margin right on solo glyphicon */\n    margin-right: 0;\n}\r\n\r\n/* ************************************ STRUCTURE ******* */\r\n\r\nbody {\n    background: #dd3f25;\n    background: -webkit-gradient(linear,  left top, right top,  from(#dd3f25),color-stop(50%, #dd3f25),color-stop(50%, #ed5137),to(#ed5137));\n    background: linear-gradient(to right,  #dd3f25 0%,#dd3f25 50%,#ed5137 50%,#ed5137 100%);\n    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#dd3f25', endColorstr='#ed5137',GradientType=1 );\n\n}\r\n\r\n.hr {\n    width: 100%;\n    height: 1px;\n    /*border-top: 1px solid rgba(0,0,0,0.15);*/\n    -webkit-box-shadow: 0 -1px 1px 0 rgba(0,0,0,0.15);\n    box-shadow: 0 -1px 1px 0 rgba(0,0,0,0.15);\n    background-color: rgba(255,255,255,0.15);\n\n    margin-top: 1em;\n    margin-bottom: 1em;\n}\r\n\r\n.flex {\n    -webkit-display: flex;\n    -moz-display: flex;\n    -ms-display: flex;\n    display: -webkit-box;\n    display: flex;\n    -ms-align-content: flex-start;\n    -ms-flex-line-pack: start;\n        align-content: flex-start;\n    -moz-align-items: stretch;\n    -ms-align-items: stretch;\n    -webkit-box-align: stretch;\n        -ms-flex-align: stretch;\n            align-items: stretch;\n}\r\n\r\n#header {\n    position: relative;\n\n    /*\n    background-color: #ed5137;\n    background-image: url(\"../img/header_red_wave_full.png\");\n    background-repeat: no-repeat;\n    background-position: center top;\n    */\n}\r\n\r\n#header #mobile-menu-btn {\n    position: absolute;\n    left: 10px;\n    top: 50%;\n\n    padding: 5px;\n    width: 40px;\n    height: 40px;\n    line-height: 30px;\n    border: 0;\n    text-align: center;\n    font-size: 30px;\n    background: none;\n    color: #ffffff;\n    -webkit-transform: translateY(-50%);\n    transform: translateY(-50%);\n\n    display: none; /* ONLY FOR MOBILE DEVICES */\n}\r\n\r\n#header #mobile-menu-btn * {\n    margin: 0;\n    top: 0;\n}\r\n\r\n#header .container {\n    background-color: #ed5137;\n    background-image: url(\"data:image/png;base64,\");\n    background-repeat: no-repeat;\n    background-position: center top;\n}\r\n\r\n#header .logo {\n    float: left;\n    display: block;\n    margin-top: 10px;\n    margin-left: 115px;\n}\r\n\r\n#header .logo img {\n    display: block;\n    margin: 0;\n}\r\n\r\n#header .desc {\n    float: right;\n    text-align: right;\n    height: 140px;\n    color: #861b09;\n\n    padding-right: 230px;\n    background-image: url('Reinhart_portrait.7a1e6edb71c9db05fcc4.png');\n    background-repeat: no-repeat;\n    background-position: right top;\n}\r\n\r\n#header .desc p:first-child {\n    margin-top: 48px;\n}\r\n\r\n#header .desc .link {\n    color: #ffffff;\n}\r\n\r\n#mobile-back-layer {\n    position: fixed;\n    z-index: 9;\n    top: 0; bottom: 0;\n    left: 0; right: 0;\n    height: 100%;\n    \n    background-color: rgba(0, 0, 0, 0.5);\n\n    -webkit-transition: all 0.4s ease;\n    transition: all 0.4s ease;\n\n    /* USED FOR MOBILE DEVICES ONLY */\n    opacity: 0;\n    visibility: hidden;\n}\r\n\r\n#mobile-close-btn {\n    visibility: hidden;\n    opacity: 0; /* USED FOR MOBILE DEVICES ONLY */\n}\r\n\r\n#nav .menu {\n    margin-top: 1em;\n}\r\n\r\n#nav .menu {\n    border-top: 7px double #ed5137;\n    border-bottom: 7px double #ed5137;\n    padding-top: 1em;\n    padding-bottom: 2.4em;\n}\r\n\r\n#nav .menu li {\n    font-family: \"thirsty_roughregular_one\", Georgia, \"Times New Roman\", Times, serif;\n}\r\n\r\n#nav .menu li a {\n    display: block;\n    color: #ffffff;\n    font-size: 1.4em;\n\n    background-image: url(\"data:image/png;base64,\");\n    background-repeat: no-repeat;\n    background-position: 4px 50%;\n    line-height: 20px;\n    padding: 30px 20px 10px 58px;\n\n    -webkit-transition: all 0.2s ease-out;\n    transition: all 0.2s ease-out;\n}\r\n\r\n#nav .menu .page_hom a {\n    background-image: url(\"data:image/png;base64,\");\n    height: 80px;\n    text-indent: 6px;\n}\r\n\r\n#nav .menu .page_rec a {\n    background-image: url(\"data:image/png;base64,\");\n    height: 80px;\n    text-indent: 6px;\n}\r\n\r\n/*#nav .menu .page_ing a {\n    background-image: url(\"../img/menu_icon_ingredients.png\");\n}\n#nav .menu .page_com a {\n    background-image: url(\"../img/menu_icon_users.png\");\n}*/\r\n\r\n#nav .menu li a:hover {\n    text-indent: 11px;\n}\r\n\r\n/*\n#nav .menu .page_hom a:hover {\n    background-image: url(\"../img/menu_icon_accueil_hover.png\");\n}\n#nav .menu .page_rec a:hover {\n    background-image: url(\"../img/menu_icon_recettes_hover.png\");\n}\n#nav .menu .page_ing a:hover {\n    background-image: url(\"../img/menu_icon_ingredients_hover.png\");\n}\n#nav .menu .page_com a:hover {\n    background-image: url(\"../img/menu_icon_users_hover.png\");\n}*/\r\n\r\n#nav .account-area {\n    margin-top: 1em;\n}\r\n\r\n#nav .account-area .button {\n    background-color: #ffffff;\n    border: 3px solid #861b09;\n    color: #000000;\n    padding: 6px;\n    text-align: center;\n    display: inline-block;\n    *display: inline;\n    *zoom: 1;\n    vertical-align: middle;\n    margin-bottom: 0.5em;\n    font-family: \"thirsty_roughregular_one\", Georgia, \"Times New Roman\", Times, serif;\n    border-radius: 8px;\n    font-size: 1.4em;\n    width: 100%;\n    font-weight: normal;\n    line-height: 26px;\n}\r\n\r\n#nav .account-area .button .icon {\n    display: inline-block;\n    *display: inline;\n    *zoom: 1;\n    vertical-align: middle;\n  \n    background-repeat: no-repeat;\n    background-position: 50% 50%;\n    background-size: 26px auto;\n    width: 26px;\n    height: 26px;\n    line-height: 0;\n    margin-right: 0.3em;\n    position: relative;\n    top: -2px;\n}\r\n\r\n#nav .account-area .button.deconnect {\n    background-color: transparent;\n    color: #861b09;\n}\r\n\r\n#nav .account-area .button.deconnect:hover {\n    color: #5B0D01;\n}\r\n\r\n#nav .account-area .button .glyphicon {\n    font-size: 0.9em;\n}\r\n\r\n#footer {\n    background-color: #862e20;\n    color: #ffffff;\n    background-size: auto 35px;\n    background-repeat: repeat-x;\n    background-position: left 10px;\n    padding-top: 20px;\n}\r\n\r\n#footer .hr {\n    background-color: #932f1f;\n    margin-top: 2em;\n}\r\n\r\n#footer .footer-hero-wrap {\n    -webkit-display: flex;\n    -moz-display: flex;\n    -ms-display: flex;\n    display: -webkit-box;\n    display: flex;\n    -ms-align-content: flex-start;\n    -ms-flex-line-pack: start;\n        align-content: flex-start;\n    -moz-align-items: stretch;\n    -ms-align-items: stretch;\n    -webkit-box-align: stretch;\n        -ms-flex-align: stretch;\n            align-items: stretch;\n}\r\n\r\n#fo